.contactForm {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 730px;
  height: 190px;
  .inputs {
    width: 100%;
    height: 100px;
    display: grid;
    gap: 30px;
    grid-template:
      'name company message'
      'phone mail message';
    .name {
      grid-area: name;
    }
    .company {
      grid-area: company;
    }
    .message {
      grid-area: message;
    }
    .phone {
      grid-area: phone;
    }
    .mail {
      grid-area: mail;
    }
  }
  .botForm {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .formText {
      font-weight: 400;
      font-size: 12px;
      line-height: 150%;
      color: #989898;
      white-space: pre-line;
    }
  }
}
